import * as React from 'react';
import './component-style.scss';
import Select from 'react-select';
import { DropdownChoice } from '../Models';
import { Option } from 'react-select/src/filters';

interface IProps {
    placeholder?: string;
    diabled?: boolean;
    value: DropdownChoice;
    choices: DropdownChoice[];
    error?: boolean;
    onChange: (value: DropdownChoice) => void;
}

export class Dropdown extends React.Component<IProps> {

    constructor(props: IProps) {
        super(props);
    }

    shouldComponentUpdate(props: IProps, _state: any) {
        return (
            (this.props.placeholder != props.placeholder) ||
            (this.props.diabled != props.diabled) ||
            (this.props.value != props.value) ||
            (this.props.choices != props.choices) ||
            (this.props.error != props.error)
        );
    }

    render(): JSX.Element {

        return (
            <div className='dropdown'>
                {this.props.placeholder ? (
                    <div className='text'>{this.props.placeholder}</div>
                ) : null}
                
                <Select
                    className='actual-dropdown'
                    classNamePrefix={this.props.error ? 'actual-dropdown-error' : 'actual-dropdown'}
                    isDisabled={this.props.diabled ? this.props.diabled : false}
                    value={this.props.value}
                    options={this.props.choices} 
                    filterOption={(option: Option, rawInput: string) => {
                        const hasNoInput = (rawInput.length == 0);
                        const choicesAreShort = (this.props.choices.length <= 20);
                        const inputIsShort = (rawInput.length < 3);
                        const isInputMatch = (option.label.toLowerCase().includes(rawInput.toLowerCase()));
                        if (choicesAreShort) {
                            if (hasNoInput) {
                                return true;
                            }
                            return isInputMatch;
                        } else {
                            if (!inputIsShort) {
                                return isInputMatch;
                            }
                            return false;
                        }
                    }}
                    onChange={(choice: DropdownChoice) => {
                        this.props.onChange(choice);
                    }} />
                    
            </div>
        );
    }
}