import * as React from 'react';
import Grid from '@material-ui/core/Grid';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import Link from '@material-ui/core/Link';

// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch } from '@fortawesome/free-solid-svg-icons';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { GlobalFunction } from './GlobalFunction';
import { MemberData, TicketData } from 'Pages/CustomerCarePage/Models';
import { TextButton } from './Components/TextButton';

interface IProps {
    memberData?: MemberData;
    ticketData?: TicketData;
    ticketStatus: 'Create' | 'Update' | 'Resolve';
    ticketNo?: string;
}

interface IStates {
    searchText: string;
}

export class MemberDataSection extends React.Component<IProps, IStates> {

    recentTicketTable: {
        header: string[];
        rows: string[][];
    }

    constructor(props: IProps) {
        super(props);

        this.state = {
            searchText: ''
        };

        this.recentTicketTable = {
            header: ['Ticket ID', 'Type', 'Last Update'],
            rows: [
                ['755012', 'Inquiry', 'Aug 16, 2019'], 
                ['752087', 'Inquiry', 'Aug 16, 2019'],
                ['752054', 'Inquiry', 'Aug 16, 2019']
            ]
        }
    }

    render(): JSX.Element {
        
        return (
            <div className='member-info'>
                {this.props.ticketStatus === 'Create' ? (
                    <div>
                        {this.renderSearchArea()}
                        <div className='line-border' />
                    </div>

                ) : this.props.ticketStatus === 'Update' ||
                    this.props.ticketStatus === 'Resolve' ? (
                    this.renderTopArea()
                ) : (null)}
                
                {this.props.memberData ? (
                    this.renderMemberInfo(this.props.memberData)
                ) : (null)}
                {this.props.ticketData ? (
                    this.renderTicketData(this.props.ticketData)
                ) : (null)}
                <div className='section' />
            </div>
        );
    }

    renderSearchArea(): JSX.Element {
        return (
            <div className='section'>
                <Breadcrumbs className='bread-crumbs'>
                    <Link href='#/customer-care'>TICKETS</Link>
                    <div className='current-text'>CREATE A TICKET</div>
                </Breadcrumbs>

                <div className='header-1'>Create Ticket</div>

                <InputField 
                    placeholder='Search Member/Client ID'
                    value={this.state.searchText}
                    onChange={(value: string) => {
                        this.setState({searchText: value});
                    }} />

                <TextButton 
                    icon={faSearch}
                    label='Advance Search'
                    onClick={() => {

                    }}
                    />
                {/* <div className='text'>
                    <FontAwesomeIcon icon={faSearch}/> Advanced Search
                </div>  */}
            </div>
        );
    }

    renderTopArea(): JSX.Element {
        return (
            <div className='section'>
                <Breadcrumbs className='bread-crumbs'>
                    <Link href='#/customer-care'>TICKETS</Link>
                    <Link className='current-text'>{this.props.ticketStatus.toUpperCase() + ' TICKET'}</Link>
                </Breadcrumbs>

                <span className='header-1'>{this.props.ticketStatus + ' Ticket ' + this.props.ticketNo}</span>

            </div>
        );
    }

    renderMemberInfo(memberData: MemberData): JSX.Element {
        const name = 
            memberData.title + ' ' +
            memberData.first_name + ' ' + 
            GlobalFunction.getIntials(memberData.middle_name) + ' ' +
            memberData.last_name + ' ' +
            memberData.suffix;
        const birthDate = GlobalFunction.toCommonDateString(new Date(memberData.date_of_birth));
        const age = GlobalFunction.getAge(new Date(memberData.date_of_birth));
        const gender = (memberData.gender === 'M' ? 'Male' : 'Female');
        const civilStatus = (memberData.civil_status === 'S' ? 'Single' : 'Married');
        return (
            <div className='section'>
                <div className='circle' style={{backgroundColor: '#3AB77D'}}></div>
                <div style={{display: 'inline-block'}}>
                <div className='header-2'>{memberData.member_card_no}</div>
                </div>
                <div className='text'>{name}</div>
                <div className='text'>{"Happytime Technology Inc."}</div>
                <div className='text'>{'Rank and File - Principal'}</div>
                <Grid container>
                    <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>{"R&B"}</div>
                        <div className='small-text'>{"Private Suite"}</div>
                    </Grid>
                    <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>{"R&B Limit"}</div>
                        <div className='small-text'>{"PHP 15,000"}</div>
                    </Grid>
                    <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>MBL LIMIT</div>
                        <div className='small-text'>{"PHP 15,000"}</div>
                    </Grid>
                    <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>PEC LIMIT</div>
                        <div className='small-text'>{"PHP 15,000"}</div>
                    </Grid>
                    <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>BIRTHDATE (AGE)</div>
                        <div className='small-text'>{birthDate + " (" + age + ")"}</div>
                    </Grid>
                    <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>GENDER / STATUS</div>
                        <div className='small-text'>{gender + " / " + civilStatus}</div>
                    </Grid>
                </Grid>
                <div>
                <CustomTable
                    className='custom-table-small'
                    placeholder='OPEN/PENDING TICKETS'
                    cols={this.recentTicketTable.header}
                    rows={this.recentTicketTable.rows} />
            </div>
            </div>
        );
    }

    renderTicketData(ticketData: TicketData): JSX.Element {
        return (
            <div className='section'>
                <div className='sub-section'>
                    <div className='light-text'>TICKET TYPE</div>
                    <div className='small-text'>{ticketData.ticketType.label}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>SPECIFIC TYPE</div>
                    <div className='small-text'>{ticketData.specificType.label}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>URGENCY</div>
                    <div className='small-text'>{ticketData.urgency.label}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>ASSIGNED TO</div>
                    <div className='small-text'>{ticketData.agent.label}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>RECEIVED VIA</div>
                    <div className='small-text'>{ticketData.ticketSource.label}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>RECEIVED FROM</div>
                    <div className='small-text'>{ticketData.callerName}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>NUMBER/EMAIL</div>
                    <div className='small-text'>{ticketData.contactNo}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>CALL TRANSACTION NO.</div>
                    <div className='small-text'>{ticketData.transactionNo}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>CREATED BY</div>
                    <div className='small-text'>{ticketData.createdBy.name}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>DATE AND TIME CREATED</div>
                    <div className='small-text'>{GlobalFunction.toDateAndTime(ticketData.dateCreated)}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>DATE AND TIME LAST UPDATED</div>
                    <div className='small-text'>{GlobalFunction.toDateAndTime(ticketData.dateUpdated)}</div>
                </div>
            </div>
        );
    }
}