
import * as React from 'react';
import Grid from '@material-ui/core/Grid';

import './../style.scss';
import { MemberDataSection } from '../MemberDataSection';
import { API } from './../API';
import { ConfinementSection } from './Components/ConfinementSection';
import { AvailmentSection } from './Components/AvailmentSection';
import { CircularProgress } from '@material-ui/core';
import { TicketInfoSection } from './Components/TicketInfoSection';
import { TicketData, MemberData, DropdownChoice, ConfinementDetails, InquiryData, AvailmentDetails, ConsultationDetails } from '../Models';
import { SaveTicketFooter } from './Components/SaveTicketFooter';
import { InquirySection } from './Components/InquirySection';
import { Payloads } from '../Payloads';
import { ConsultationSection } from './Components/ConsultationSection';
import { Processmaker } from '../Processmaker';

interface IProps {
    history: any;
}

interface IStates {
    loading: boolean;
    createTicketAttemp: boolean;

    /* Data coming from database */
    memberData?: MemberData;

    /* Data to store to database */
    ticket: TicketData;
    availment: AvailmentDetails;
    confinement: ConfinementDetails;
    consultation: ConsultationDetails;
    inquiries: InquiryData[];
}

export class CreateTicketPage extends React.Component<IProps, IStates> {

    icd10Codes: DropdownChoice[];
    cptCodes: DropdownChoice[];
    facilities: DropdownChoice[];
    doctors: DropdownChoice[];
    agents: DropdownChoice[];
    
    constructor(props: IProps) {
        super(props);

        this.state = {
            loading: false,
            createTicketAttemp: false,

            ticket: {
                ticketChannel: { value: '', label: '' },
                transactionNo: '',
                ticketSource: { value: '', label: '' },
                memberName: '',
                clientName: '',
                brokerName: '',
                facility: { value: '', label: '' },
                department: '',
                callerName: '',
                contactNo: '',
                ticketType: { value: '', label: '' },
                specificType: { value: '', label: '' },
                urgency: { value: '', label: '' },
                agent: { value: '', label: '' },
                additionalNotes: '',
                additionalFiles: [],
                createdBy: { _id: '1', name: 'Jilmer Cariaso - Static' },
                dateCreated: new Date(),
                dateUpdated: new Date()
            },
            availment: {
                provider: { value: '', label: '' },
                diagnoses: []
            },
            confinement: {
                provider: { value: '', label: '' },
                dateAdmitted: new Date(),
                confinementKind: { value: '', label: '' },
                roomType: '',
                costPerDay: 0,
                doctor: { value: '', label: '' },
                initialDiagnosis: { value: '', label: '' },
                ticketNo: '',
                amount: 0
            },
            consultation: {
                provider: { value: '', label: '' },
                diagnosis: { value: '', label: '' },
                doctor: { value: '', label: '' },
                notes: ''
            },
            inquiries: []
        };

        this.facilities = [];
        this.agents = [];
        this.doctors = [];
        this.cptCodes = [];
        this.icd10Codes = [];
    }

    componentWillUpdate(_props: IProps, _state: IStates) {
        
    }

    componentDidMount() {
        // this.setState({loading: true});
        API.fakeApiCall('/members/1', 'GET', 
            (response) => {
                this.setState({memberData: response});
            },
            (error) => {
                console.log(error);
            });

        API.get('/facilities',
            (response) => {
                this.facilities = response.map(facility => {
                    return { 
                        value: facility._id, 
                        detail: facility.facility_id, 
                        label: facility.name 
                    }
                });
            },
            (error) => {
                console.log(error);
            });

        API.get('/icd10-codes?filter[limit]=1000',
            (response) => {
                this.icd10Codes = response.map(icd10Code => {
                    return { 
                        value: icd10Code._id, 
                        detail: icd10Code.full_code,
                        label: icd10Code.abbreviated_description 
                    };
                });
            },
            (error) => {
                console.log(error);
            });

        API.get('/cpts?filter[limit]=1000',
            (response) => {
                this.cptCodes = response.map(cptCode => {
                    return {
                        value: cptCode._id, 
                        detail: cptCode.code.toString(),
                        label: cptCode.label 
                    };
                });
            }, 
            (error) => {
                console.log(error);
            });

        API.get('/doctors',
            (response) => {
                this.doctors = response.map(doctor => {
                    return {
                        value: doctor._id, 
                        detail: doctor.type,
                        label: ('Dr. ' + doctor.first_name +  ' ' + doctor.last_name)
                    };
                });
            }, 
            (error) => {
                console.log(error);
            });
    }

    render(): JSX.Element {

        return (
            <Grid container className='main'>
                {this.state.loading ? <CircularProgress /> : null}

                <MemberDataSection 
                    memberData={this.state.memberData}
                    ticketStatus='Create' />
                
                <TicketInfoSection 
                    ticket={this.state.ticket} 
                    facilities={this.facilities}
                    agents={this.agents}
                    createTicketAttemp={this.state.createTicketAttemp}
                    refreshTicketState={() => {
                        this.setState({ticket: this.state.ticket});
                    }} />

                {this.state.ticket.ticketType.value === '1' ? (
                
                    this.state.ticket.specificType.value === '1' ? (  
                        <AvailmentSection
                            availment={this.state.availment}
                            facilities={this.facilities}
                            doctors={this.doctors}
                            icd10Codes={this.icd10Codes}
                            cptCodes={this.cptCodes}
                            createTicketAttemp={this.state.createTicketAttemp}
                            refreshAvailmentState={() => {
                                this.setState({availment: this.state.availment});
                            }} />

                    ) : this.state.ticket.specificType.value === '2' ? ( 
                        <ConfinementSection
                            confinement={this.state.confinement}
                            facilities={this.facilities}
                            doctors={this.doctors}
                            cptCodes={this.icd10Codes}
                            createTicketAttemp={this.state.createTicketAttemp}
                            refreshConfinementState={() => {
                                this.setState({confinement: this.state.confinement});
                            }} />

                    ) : this.state.ticket.specificType.value === '4' ? ( 
                        <ConsultationSection
                            consultation={this.state.consultation}
                            facilities={this.facilities}
                            doctors={this.doctors}
                            icd10Codes={this.icd10Codes}
                            createTicketAttemp={this.state.createTicketAttemp}
                            refreshConsultationState={() => {
                                this.setState({consultation: this.state.consultation});
                            }} />
                    ) : (null)

                ) : this.state.ticket.ticketType.value === '2' ? (
                    <InquirySection
                        inquiries={this.state.inquiries}
                        refreshInquiryState={() => {
                            this.setState({inquiries: this.state.inquiries});
                        }} />
                ) : (null)}
                
                <SaveTicketFooter 
                    onSaveForNow={() => null} 
                    onSubmitTicket={() => {
                        this.setState({createTicketAttemp: true});
                        const ticket = this.state.ticket;
                        const confinement = this.state.confinement;
                        const availment = this.state.availment;
                        const inquiries = this.state.inquiries;

                        /* Confinement Details must have all contents */
                        if (ticket.ticketType.value === '1' &&
                        ticket.specificType.value === '2') {
                                if (confinement.provider.value === '' ||
                                    confinement.amount == 0 ||
                                    confinement.confinementKind.value === '' ||
                                    confinement.costPerDay == 0 ||
                                    confinement.doctor.value === '' ||
                                    confinement.initialDiagnosis.value === '' ||
                                    confinement.roomType === '' ||
                                    confinement.ticketNo === '') {
                                    return;
                                }
                            }

                        const payload = Payloads.getPostTicketPayload(ticket, confinement, availment, inquiries);

                        console.log(payload);
                        API.post('/customer-care/tickets', payload,
                            (response) => {
                                console.log(response);
                                if (response['_id']) {
                                    console.log('Ticket No: '+response['id']);
                                    let is_availment = false;
                                    if (this.state.ticket.ticketType.value==='1'){
                                        is_availment=true;
                                    }
                                    let trigger_id = "3905447335db2a32daafd33048658514"; //should refer to redux createtickettrigger_id!!!
                                    let body = {
                                        "pro_uid" : "1538417025dae8dae9ff7a9016454302", //should refer to redux createticketproject_id!!!
                                        "tas_uid":"6092309955dae8db0a85907021073537",   //should refer to redux createticketstarttask_id!!!
                                        "ticket_no":response['_id'],
                                        "can_be_resolved_imm":0,
                                        "is_availment":is_availment
                                    };
                                    console.log('Body: '+body);
                                    Processmaker.post('/cases',body,
                                        (response) => {
                                            if (response['app_uid']){
                                                let case_id = response['app_uid'];
                                                Processmaker.put('/cases/'+case_id+'/execute-trigger/'+trigger_id,body,
                                                    (response) => {
                                                        console.log('Trigger Response: '+response);
                                                        Processmaker.put('/cases/'+case_id+'/variable',body,
                                                            (response) => {
                                                                console.log('Set Variable Response: '+response);
                                                                this.props.history.push('#/customer-care/');
                                                                window.location.reload();
                                                            }, 
                                                            (error) => {
                                                                console.log(error);
                                                            }
                                                        );
                                                    }, 
                                                    (error) => {
                                                        console.log(error);
                                                    }
                                                );
                                            }
                                            this.props.history.push('#/customer-care/');
                                            window.location.reload();
                                        }, 
                                        (error) => {
                                            console.log(error);
                                        }
                                    ); 
                                }     
                            },
                            (error) => {
                                console.log(error);
                            });
                    }} />
            </Grid>
        );
    }
}