import * as React from 'react';
import { match } from "react-router-dom";
import {
    UpdateData, 
    AccommodationData, 
    DiagnosisData, 
    PhysicianData, 
    ProcedureData,
    ItemData,
    FileData
} from './UpdateTicketProps';
import { Grid, CircularProgress } from '@material-ui/core';
import { SummarySection } from './Components/SummarySection';
import { UpdateSection } from './Components/UpdateSection';
import { API } from '../API';
import { AccommodationsSection } from './Components/AccommodationsSection';
import { DiagnosesSection } from './Components/DiagnosesSection';
import { PhysiciansSection } from './Components/PhysiciansSection';
import { ProceduresSection } from './Components/ProceduresSection';
import { OtherDetailsSection } from './Components/OtherDetailsSection';
import { DischargeDetailsSection } from './Components/DischargeDetailsSection';
import { UploadedDocumentsSection } from './Components/UploadedDocumentsSection';
import { MemberDataSection } from '../MemberDataSection';
import { DropdownChoice, TicketData, MemberData, InquiryData } from '../Models';
import { UpdateTicketFooter } from './Components/UpdateTicketFooter';
import { UpdateInquirySection } from './Components/UpdateInquirySection';
import { CustomTabs } from '../Components/CustomTab';
import { Payloads } from '../Payloads';
import { Processmaker } from '../Processmaker';

interface IProps {
    required: string;
    match: match<MatchParams>;
    history: any;
}

interface MatchParams {
    id: string;
}

interface IStates {
    tabSelected: number;
    loadedResources: number;

    ticketNo: string;
    memberData?: MemberData;
    ticket: TicketData;
    
    /* Inpatient states */
    provider: DropdownChoice;
    dateAdmitted: Date;
    confinementKind: DropdownChoice;
    giftGiven: boolean;
    amount: number; // No use
    initialDiagnosis: string; // No use
    physician: string; // No use
    coverage: DropdownChoice;
    philhealthAmount: number;
    finalBill: number;
    dischargeDate: Date;
    primaryDiagnosisIndex: number;
    
    updates: UpdateData[];
    accommodations: AccommodationData[];
    diagnoses: DiagnosisData[];
    physicians: PhysicianData[];
    procedures: ProcedureData[];
    items: ItemData[];
    files: FileData[];

    /* Inquiry States */
    inquiriesData: InquiryData[];
}

export class UpdateTicketPage extends React.Component<IProps, IStates> {

    static RESOURCES_TO_LOAD = 5;

    tabList: { value: number, label: string }[];
    icd10Codes: DropdownChoice[];
    cptCodes: DropdownChoice[];
    facilities: DropdownChoice[];
    doctors: DropdownChoice[];
    agents: DropdownChoice[];

    constructor(props: IProps) {
        super(props);

        this.state = {
            tabSelected: 1,
            loadedResources: 0,

            ticketNo: '',
            ticket: {
                ticketChannel: { value: '', label: '' },
                transactionNo: '',
                ticketSource: { value: '', label: '' },
                memberName: '',
                clientName: '',
                brokerName: '',
                facility: { value: '', label: '' },
                department: '',
                callerName: '',
                contactNo: '',
                ticketType: { value: '', label: '' },
                specificType: { value: '', label: '' },
                urgency: { value: '', label: '' },
                agent: { value: '', label: '' },
                additionalNotes: '',
                additionalFiles: [],
                createdBy: { _id: '1', name: 'Jilmer Cariaso - Static' },
                dateCreated: new Date(),
                dateUpdated: new Date()
            },

            provider: { value: '', label: '' },
            dateAdmitted: new Date(),
            confinementKind: { value: '', label: '' },
            giftGiven: false,
            amount: 0,
            initialDiagnosis: '',
            physician: '',
            coverage: { value: '', label: '' },
            philhealthAmount: 0,
            finalBill: 0,
            dischargeDate: new Date(),
            primaryDiagnosisIndex: 0,

            updates: [],
            accommodations: [],
            diagnoses: [],
            physicians: [],
            procedures: [],
            items: [],
            files: [],

            inquiriesData: []
        };

        this.tabList = [
            { value: 1, label: 'Summary' }, 
            { value: 2, label: 'Updates' }, 
            { value: 3, label: 'Room Accommodations' }, 
            { value: 4, label: 'Diagnoses' }, 
            { value: 5, label: 'Physicians' }, 
            { value: 6, label: 'Procedures' },  
            { value: 7, label: 'PhilHealth & Discharge Details' }, 
            { value: 8, label: 'Other Details' }, 
            { value: 9, label: 'Uploaded Documents' }
        ];

        this.facilities = [];
        this.agents = [];
        this.doctors = [];
        this.cptCodes = [];
        this.icd10Codes = [];
    }

    componentDidMount() {
        API.fakeApiCall('/members/1', 'GET', 
            (response) => {
                this.setState({memberData: response});
            },
            (error) => {
                console.log(error);
            });

        API.get('/customer-care/tickets/' + this.props.match.params['ticketId'], 
            (response) => {
                this.setState({loadedResources: this.state.loadedResources + 1});
                const ticketCategory = response.ticket_category;
                const confinementDetails = ticketCategory.confinement_details;
                const inquiryDetails = ticketCategory.inquiry_details;

                this.setState({ticket: {
                    ticketChannel: { value: '', label: response.received_via },
                    transactionNo: response.transaction_no,
                    ticketSource: { value: '', label: response.received_from },
                    memberName: response.member_name,
                    clientName: response.corporate_client,
                    brokerName: response.broker,
                    facility: { value: '', label: response.hospital_name },
                    department: response.department,
                    callerName: response.caller_name,
                    contactNo: response.contact_no,
                    ticketType: { value: '', label: ticketCategory.ticket_type },
                    specificType: { value: '', label: ticketCategory.specific_type },
                    urgency: { value: '', label: ticketCategory.urgency },
                    agent: { value: '', label: ticketCategory.assign_to }, // Load from db
                    additionalNotes: response.additional_notes,
                    additionalFiles: [],
                    createdBy: { _id: '1', name: 'Jilmer Cariaso - Static' },
                    dateCreated: new Date(response.date_created),
                    dateUpdated: new Date(response.date_updated)
                }});

                if (confinementDetails) {
                    this.setState({ticketNo: confinementDetails.ticket_number});
                    const providerData = confinementDetails.provider.split(' - ');
                    this.setState({provider: { value: '', detail: providerData[0], label: providerData[1] }});
                    this.setState({dateAdmitted: new Date(confinementDetails.date_admitted )});
                    this.setState({confinementKind: { value: '', label: confinementDetails.confinement_kind }});
                    this.setState({giftGiven: confinementDetails.gift_given});
                    this.setState({amount: confinementDetails.amount});
                    this.setState({initialDiagnosis: confinementDetails.initial_diagnosis});
                    this.setState({physician: confinementDetails.physician});
                    this.setState({coverage: { value: '', label: confinementDetails.coverage }});
                    this.setState({philhealthAmount: confinementDetails.philhealth_amount});
                    this.setState({finalBill: confinementDetails.final_bill});
                    this.setState({dischargeDate: new Date(confinementDetails.discharge_date)});
                    this.setState({updates: confinementDetails.updates.map(value => {
                        return {
                            remarks: value.remarks,
                            date: new Date(value.date),
                            hospitalBill: value.hospital_bill
                        };
                    })});
                    this.setState({accommodations: confinementDetails.room_accommodation.map(value => {
                        return {
                            roomType: value.room_type,
                            days: value.no_of_days,
                            costPerDay: value.cost
                        };
                    })});
                    this.setState({diagnoses: confinementDetails.diagnoses.map(value => {
                        return {
                            diagnosis: { value: value.code, detail: value.code, label: value.name },
                            remarks: value.remarks,
                            isFinal: value.is_final,
                            isPrimary: value.is_primary
                        };
                    })});
                    confinementDetails.diagnoses.map((value, index) => {
                        if (value.is_primary) {
                            this.setState({primaryDiagnosisIndex: index});
                        }
                    });
                    const physicians = confinementDetails.diagnoses.map(diagnosis => {
                        return diagnosis.doctors.map(doctor => {
                            return {
                                physician: {
                                    specialization: doctor.specialization,
                                    name: doctor.name,
                                    type: doctor.type
                                },
                                diagnosis: { value: diagnosis.code, detail: diagnosis.code, label: diagnosis.name },
                                remarks: doctor.remarks,
                                visits: doctor.no_of_visits,
                                amount: doctor.fee
                            };
                        });
                    });
                    this.setState({physicians: [].concat(...physicians)});
                    const procedures = confinementDetails.diagnoses.map(diagnosis => {
                        return diagnosis.procedures.map(procedure => {
                            return {
                                procedure: { value: procedure.code, detail: procedure.code, label: procedure.name },
                                diagnosis: { value: diagnosis.code, detail: diagnosis.code, label: diagnosis.name },
                                physician: { value: '', label: procedure.doctor },
                                date: new Date(procedure.date_added),
                                remarks: procedure.notes,
                                amount: parseInt(procedure.amount)
                            };
                        });
                    });
                    this.setState({procedures: [].concat(...procedures)});
                    this.setState({items: confinementDetails.others.map(value => {
                        return {
                            name: value.name,
                            type: value.type,
                            amount: value.amount
                        };
                    })});
                }
                
                if (inquiryDetails) {
                    this.setState({inquiriesData: inquiryDetails.inquiry_list.map(value => {
                        return {
                            inquiry: value.inquiry,
                            detail: value.detail,
                            specificDetail: value.specific_detail,
                            answer: value.answer,
                            remarks: value.remarks,
                            relatedTickets: value.related_tickets,
                            supportDocs: value.support_docs
                        };
                    })});
                }

            },
            (error) => {
                console.log(error);
            });

            API.get('/facilities',
            (response) => {
                this.setState({loadedResources: this.state.loadedResources + 1});
                this.facilities = response.map(facility => {
                    return { 
                        value: facility._id, 
                        detail: facility.facility_id, 
                        label: facility.name 
                    }
                });
            },
            (error) => {
                console.log(error);
            });

        API.get('/icd10-codes?filter[limit]=1000',
            (response) => {
                this.setState({loadedResources: this.state.loadedResources + 1});
                this.icd10Codes = response.map(icd10Code => {
                    return { 
                        value: icd10Code._id, 
                        detail: icd10Code.full_code,
                        label: icd10Code.abbreviated_description 
                    };
                });
            },
            (error) => {
                console.log(error);
            });

        API.get('/cpts?filter[limit]=1000',
            (response) => {
                this.setState({loadedResources: this.state.loadedResources + 1});
                this.cptCodes = response.map(cptCode => {
                    return {
                        value: cptCode._id, 
                        detail: cptCode.code,
                        label: cptCode.label 
                    };
                });
            }, 
            (error) => {
                console.log(error);
            });

        API.get('/doctors',
            (response) => {
                this.setState({loadedResources: this.state.loadedResources + 1});
                this.doctors = response.map(doctor => {
                    return {
                        value: doctor._id, 
                        detail: doctor.type,
                        label: ('Dr. ' + doctor.first_name +  ' ' + doctor.last_name)
                    };
                });
            }, 
            (error) => {
                console.log(error);
            });
    }

    render(): JSX.Element {
        return (
            <Grid container className='main'>
                {this.state.loadedResources != UpdateTicketPage.RESOURCES_TO_LOAD ? (
                    <div style={{backgroundColor: '#00000055', zIndex: 100, position: 'fixed', width: '100%', height: '100%'}}>
                        <div style={{textAlign: 'center', height: '100%'}}>
                            <br /><br /><br /><br /><br />
                            <CircularProgress />
                        </div>
                    </div>
                ) : (null)}
                
                <MemberDataSection 
                    memberData={this.state.memberData}
                    ticketData={this.state.ticket}
                    ticketNo={this.state.ticketNo}
                    ticketStatus='Update' />
                {this.state.ticket.specificType.label === 'Inpatient' ? (
                    this.renderInpatientContent()
                ) : this.state.ticket.specificType.label === 'Not applicable' ? (
                    this.renderInquiryContent()
                ) : null}
                <UpdateTicketFooter 
                    onSaveForNow={() => null} 
                    onUpdateTicket={() => {
                        const payload = Payloads.getPutTicketPayload(
                            this.state.ticket, 
                            this.state.updates, 
                            this.state.accommodations,
                            this.state.diagnoses,
                            this.state.physicians,
                            this.state.procedures,
                            this.state.items,
                            this.state.provider,
                            this.state.dateAdmitted,
                            this.state.confinementKind,
                            this.state.ticketNo,
                            this.state.giftGiven,
                            this.state.coverage,
                            this.state.philhealthAmount,
                            this.state.finalBill,
                            this.state.dischargeDate,
                            this.state.primaryDiagnosisIndex
                        );

                        console.log(payload);
                        API.put('/customer-care/tickets/' + this.props.match.params['ticketId'], payload,
                            (response) => {
                                console.log(response);
                                // Processmaker.put('/cases/'+this.state.ticket_no+'/route-case', {},   //this should refer to dashboard case_id !!!
                                Processmaker.put('/cases/'+'2853194775db2bd7d5a78f1085585756'+'/route-case', {},
                                    (response) => {
                                        console.log(response);
                                        this.props.history.push('#/customer-care');
                                        window.location.reload();
                                    },
                                    (error) => {
                                        console.log(error);
                                    }
                                );
                                this.props.history.push('#/customer-care');
                                window.location.reload();
                            },
                            (error) => {
                                console.log(error);
                            });
                    }} />
            </Grid>
        );
    }

    renderInpatientContent(): JSX.Element {
        return (
            <div className='update-ticket'>
                <CustomTabs 
                    tabList={this.tabList}
                    selectedTab={this.state.tabSelected}
                    setSelectedTab={(index: number) => {
                        this.setState({tabSelected: index});
                    }} />
                    
                {this.state.tabSelected == 1 ? (
                    <SummarySection 
                        ticketNo={this.state.ticketNo} 
                        updates={this.state.updates}
                        accommodationsData={this.state.accommodations}
                        diagnosesData={this.state.diagnoses}
                        physiciansData={this.state.physicians}
                        proceduresData={this.state.procedures}
                        itemsData={this.state.items}
                        provider={this.state.provider}
                        dateAdmitted={this.state.dateAdmitted}
                        confinementKind={this.state.confinementKind}
                        coverage={this.state.coverage}
                        philhealthAmount={this.state.philhealthAmount}
                        finalBill={this.state.finalBill}
                        dischargeDate={this.state.dischargeDate}
                        setTabSelected={(value: number) => this.setState({tabSelected: value})}/>
                    

                ) : this.state.tabSelected == 2 ? (
                    <UpdateSection 
                        updates={this.state.updates}
                        addUpdate={(update: UpdateData) => {
                            this.state.updates.unshift(update);
                            this.setState({updates: this.state.updates});
                        }}
                        editUpdate={(index: number, update: UpdateData) => {
                            this.state.updates[index] = update;
                            this.setState({updates: this.state.updates});
                        }}
                        deleteUpdate={(index: number) => {
                            this.state.updates.splice(index, 1);
                            this.setState({updates: this.state.updates});
                        }} /> 

                ) : this.state.tabSelected == 3 ? (
                    this.state.ticket ? (
                        <AccommodationsSection
                            accommodations={this.state.accommodations}
                            facilitiesList={this.facilities}
                            provider={this.state.provider}
                            dateAdmitted={this.state.dateAdmitted}
                            confinementKind={this.state.confinementKind}
                            giftGiven={this.state.giftGiven}
                            addAccommodation={(accommodation: AccommodationData) => {
                                this.state.accommodations.push(accommodation);
                                this.setState({accommodations: this.state.accommodations});
                            }}
                            editAccommodation={(index: number, accommodation: AccommodationData) => {
                                this.state.accommodations[index] = accommodation;
                                this.setState({accommodations: this.state.accommodations});
                            }}
                            deleteAccommodation={(index: number) => {
                                this.state.accommodations.splice(index, 1);
                                this.setState({accommodations: this.state.accommodations});
                            }}
                            onUpdateTicketStateChange={(attrName: string, value: any) => {
                                this.setState(prevState => ({
                                    ...prevState,
                                    [attrName]: value
                                }));
                            }} />
                    ) : null

                ) : this.state.tabSelected == 4 ? (
                    this.state.ticket ? (
                        <DiagnosesSection 
                            diagnoses={this.state.diagnoses}
                            diagnosesList={this.icd10Codes}
                            primaryDiagnosisIndex={this.state.primaryDiagnosisIndex}
                            addDiagnosis={(diagnosis: DiagnosisData) => {
                                this.state.diagnoses.push(diagnosis);
                                this.setState({diagnoses: this.state.diagnoses});
                            }}
                            editDiagnosis={(index: number, diagnosis: DiagnosisData) => {
                                this.state.diagnoses[index] = diagnosis;
                                this.setState({diagnoses: this.state.diagnoses});
                            }}
                            deleteDiagnosis={(index: number) => {
                                this.state.diagnoses.splice(index, 1);
                                this.setState({diagnoses: this.state.diagnoses});
                            }}
                            setPrimaryDiagnosisIndex={(index: number) => {
                                this.setState({primaryDiagnosisIndex: index});
                            }} />
                    ) : null

                ) : this.state.tabSelected == 5 ? (
                    <PhysiciansSection 
                        physicians={this.state.physicians}
                        physiciansList={this.doctors}
                        diagnosesList={this.state.diagnoses}
                        addPhysician={(physician: PhysicianData) => {
                            this.state.physicians.push(physician);
                            this.setState({physicians: this.state.physicians});
                        }}
                        editPhysician={(index: number, physician: PhysicianData) => {
                            this.state.physicians[index] = physician;
                            this.setState({physicians: this.state.physicians});
                        }}
                        deletePhysician={(index: number) => {
                            this.state.physicians.splice(index, 1);
                            this.setState({physicians: this.state.physicians});
                        }} />

                ) : this.state.tabSelected == 6 ? (
                    <ProceduresSection
                        procedures={this.state.procedures}
                        proceduresList={this.cptCodes}
                        diagnosesList={this.state.diagnoses}
                        physiciansList={this.doctors}
                        addProcedure={(procedure: ProcedureData) => {
                            this.state.procedures.push(procedure);
                            this.setState({procedures: this.state.procedures});
                        }}
                        editProcedure={(index: number, procedure: ProcedureData) => {
                            this.state.procedures[index] = procedure;
                            this.setState({procedures: this.state.procedures});
                        }}
                        deleteProcedure={(index: number) => {
                            this.state.procedures.splice(index, 1);
                            this.setState({procedures: this.state.procedures});
                        }} />

                ) : this.state.tabSelected == 7 ? (
                    <DischargeDetailsSection 
                        coverage={this.state.coverage} 
                        philhealthAmount={this.state.philhealthAmount}
                        finalBill={this.state.finalBill} 
                        dischargeDate={this.state.dischargeDate}
                        onUpdateTicketStateChange={(attrName: string, value: any) => {
                            this.setState(prevState => ({
                                ...prevState,
                                [attrName]: value
                            }));
                        }} />
                    
                ) : this.state.tabSelected == 8 ? (
                    <OtherDetailsSection 
                        items={this.state.items}
                        addItem={(item: ItemData) => {
                            this.state.items.push(item);
                            this.setState({items: this.state.items});
                        }}
                        editItem={(index: number, item: ItemData) => {
                            this.state.items[index] = item;
                            this.setState({items: this.state.items});
                        }}
                        deleteItem={(index: number) => {
                            this.state.items.splice(index, 1);
                            this.setState({items: this.state.items});
                        }}/>
                    
                ) : this.state.tabSelected == 9 ? (
                    <UploadedDocumentsSection 
                        files={this.state.files}
                        addFile={(file: FileData) => {
                            this.state.files.push(file);
                            this.setState({files: this.state.files});
                        }}
                        editFile={(index: number, file: FileData) => {
                            this.state.files[index] = file;
                                this.setState({files: this.state.files});
                        }}
                        deleteFile={(index: number) => {
                            this.state.files.splice(index, 1);
                            this.setState({files: this.state.files});
                        }}/>
                    
                    
                    
                ) : null}
            </div>
        );
    }

    renderInquiryContent(): JSX.Element {
        return (
            <UpdateInquirySection 
                inquiries={this.state.inquiriesData}
                refreshInquiryState={() => {
                    this.setState({inquiriesData: this.state.inquiriesData});
                }} />
        );
    }
}

