import * as React from 'react';
import { Grid } from '@material-ui/core';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { ComplexTable } from 'Pages/CustomerCarePage/Components/ComplexTable';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { API } from 'Pages/CustomerCarePage/API';
import { PartialBenefitData } from 'Pages/CustomerCarePage/Models';

interface IStates {
    benefitCategory: string;
    benefitName: string;
    limitType: string;
    limit: string;

    benefitListUpdated: boolean;
}


export class CheckBenefitSection extends React.Component<{}, IStates> {

    benefits: PartialBenefitData[];

    // dummyData = [
    //     {
    //         category: 'Consultation',
    //         name: 'Medical consultation during regular clinic hour',
    //         limitType: 'None',
    //         limit: '-'
    //     },
    //     {
    //         category: 'Therapy',
    //         name: 'Speech and Physical Therapy',
    //         limitType: 'Max Session',
    //         limit: '12/year'
    //     },
    //     {
    //         category: 'Special Diagnostic Procedure',
    //         name: 'CT Scan',
    //         limitType: 'PEC',
    //         limit: '20,000'
    //     }
    // ];

    constructor(props: any) {
        super(props);

        this.state = {
            benefitCategory: '',
            benefitName: '',
            limitType: '',
            limit: '',
            benefitListUpdated: false
        }

        this.benefits = [];
    }

    componentDidMount() {
        API.get('/benefit-plans',
            (response) => {
                console.log(response);
                this.benefits = response.map(benefit => {
                    return {
                        category: benefit.type,
                        name: benefit.name,
                        limitType: (benefit.limits.length > 0 ? benefit.limits[0].type : '---'),
                        limit: (benefit.limits.length > 0 ? (
                            benefit.limits[0].limit + ' ' + benefit.limits[0].limit_unit
                        ) : '---')
                    }
                });
                this.setState({benefitListUpdated: true});
            },
            (error) => {
                console.log(error);
            });
    }

    getFilteredBenefitsData = () => {
        return (
            this.benefits
                .filter(benefit => 
                    this.state.benefitCategory === '' || 
                    benefit.category.toLowerCase().includes(this.state.benefitCategory.toLowerCase()))
                .filter(benefit => 
                    this.state.benefitName === '' || 
                    benefit.name.toLowerCase().includes(this.state.benefitName.toLowerCase()))
                .filter(benefit => 
                    this.state.limitType === '' || 
                    benefit.limitType.toLowerCase().includes(this.state.limitType.toLowerCase()))
                .filter(benefit => 
                    this.state.limit === '' || 
                    benefit.limit.toLowerCase().includes(this.state.limit.toLowerCase()))
        );
    }

    render(): JSX.Element {
        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Check Benefits</div>
                <div className='sub-section' />
                <Grid container className='sub-section' spacing={5}>
                    {this.renderSideBar()}
                    {this.renderBenefitsList()}
                </Grid>
                
            </div>
        );
    }

    renderSideBar(): JSX.Element {
        const rightBorderStyle = {
            borderRight: '#3C394A22 solid 1px'
        }
        return (
            <Grid item xs={2} style={rightBorderStyle}>
                    <div className='small-text'>Contract ID</div>
                    <div className='header-1'>{'2019-0928*'}</div>
                <div className='sub-section'>
                    <div className='small-text'>Room and Board</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>COVERAGE</div>
                    <div className='sub-section'><TextButton label='APE' onClick={() => {}} /></div>
                    <div className='sub-section'><TextButton label='PPE' onClick={() => {}} /></div>
                    <div className='sub-section'><TextButton label='Inpatient' onClick={() => {}} /></div>
                    <div className='sub-section'><TextButton label='Outpatient' onClick={() => {}} /></div>
                    <div className='sub-section'><TextButton label='Emergency' onClick={() => {}} /></div>
                </div>

                <div className='sub-section'>
                    <div className='header-1 light-text'>RIDERS</div>
                </div>

            </Grid>
        );
    }

    renderBenefitsList(): JSX.Element {

        return (
            <Grid item xs={10}>
                <InputField 
                    value=''
                    onChange={(_value: string) => {

                    }} />
                <div className='sub-section'>
                    <ComplexTable 
                        data={this.getFilteredBenefitsData()}
                        renderHeader={() => this.renderTableHeader()}
                        renderRow={(rowData: any, index: number) => 
                            this.renderTableRow(rowData, index)} />
                </div>

            </Grid>
        );
    }

    renderTableHeader(): JSX.Element {
        const headerNameStyle = {
            padding: '5px 0 0px 0'
        };

        return (
            <>
                <Grid item xs={3}>
                    <div style={headerNameStyle}>
                        <div className='header-2'>Benefit Category</div>
                        <InputField 
                            value={this.state.benefitCategory}
                            onChange={(value: string) => {
                                this.setState({benefitCategory: value});
                            }} />
                    </div>

                </Grid>
                <Grid item xs={5}>
                    <div style={headerNameStyle}>
                        <div className='header-2'>Benefit</div>
                        <InputField 
                            value={this.state.benefitName}
                            onChange={(value: string) => {
                                this.setState({benefitName: value});
                            }} />
                    </div>
                
                </Grid>
                <Grid item xs={2}>
                    <div style={headerNameStyle}>
                        <div className='header-2'>Limit Type</div>
                        <InputField 
                            value={this.state.limitType}
                            onChange={(value: string) => {
                                this.setState({limitType: value});
                            }} />
                    </div>
                
                </Grid>
                <Grid item xs={2}>
                    <div style={headerNameStyle}>
                        <div className='header-2'>Limit</div>
                        <InputField 
                            value={this.state.limit}
                            onChange={(value: string) => {
                                this.setState({limit: value});
                            }} />
                    </div>
                
                </Grid>
                
            </>
        );
    }

    renderTableRow(benefit: any, _index: number): JSX.Element {
        const cellStyle = {
            padding: '12px 0 12px 0'
        };

        return (
            <Grid container spacing={2} style={{padding: '0 20px 0 10px'}}>
                <Grid item xs={3}>
                    <div style={cellStyle}>{benefit.category}</div>
                </Grid>
                <Grid item xs={5}>
                    <div style={cellStyle}>{benefit.name}</div>
                </Grid>
                <Grid item xs={2}>
                    <div style={cellStyle}>{benefit.limitType}</div>
                </Grid>
                <Grid item xs={2}>
                    <div style={cellStyle}>{benefit.limit}</div>
                </Grid>
            </Grid>
        );
    }
}