
export class API {

    static dummyData = require('./dummy-data.json');

    //local
    static localUrl = 'https://neo-local.seerlabs.com/api';

    //stg
    static stgUrl = 'http://18.138.94.224:4101/api';
    


    static async fakeApiCall(url: string, _method: string, success: Function, _fail: Function) {
        success(API.dummyData[url]);
    }

    static async get(url: string, success: Function, fail: Function) {
        const backendUrl = (process.env.NODE_ENV === 'development' ? this.localUrl : this.stgUrl);
        fetch(backendUrl + url, {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' }
        })
        .then(response => response.json())
        .then(data => success(data))
        .catch(error => fail(error));
    }

    static async post(url: string, body: any, success: Function, fail: Function) {
        const backendUrl = (process.env.NODE_ENV === 'development' ? this.localUrl : this.stgUrl);
        fetch(backendUrl + url, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(body)
        })
        .then(response => response.json())
        .then(data => success(data))
        .catch(error => fail(error));
    }

    static async put(url: string, body: any, success: Function, fail: Function) {
        const backendUrl = (process.env.NODE_ENV === 'development' ? this.localUrl : this.stgUrl);
        fetch(backendUrl + url, {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(body)
        })
        .then(response => response)
        .then(data => success(data))
        .catch(error => fail(error));
    }
}