import * as React from 'react';
import {  Grid } from '@material-ui/core';
import { faMinusCircle, faPlus } from '@fortawesome/free-solid-svg-icons';
import { DetailedDropdown } from 'Pages/CustomerCarePage/Components/DetailedDropdown';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { ProcedureData, DropdownChoice, AvailmentDetails, DiagnosisData } from 'Pages/CustomerCarePage/Models';
import { NumberField } from 'Pages/CustomerCarePage/Components/NumberField';
import { CustomCheckbox } from 'Pages/CustomerCarePage/Components/CustomCheckbox';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { ComplexTable } from 'Pages/CustomerCarePage/Components/ComplexTable';

interface IProps {
    availment: AvailmentDetails;
    facilities: DropdownChoice[];
    doctors: DropdownChoice[];
    icd10Codes: DropdownChoice[];
    cptCodes: DropdownChoice[];
    createTicketAttemp: boolean;
    refreshAvailmentState: () => void;
}

interface IStates {
    icd10Code: DropdownChoice;
    doctor: DropdownChoice;
    icd10CodesAreLoaded: boolean;
}

export class AvailmentSection extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            icd10Code: { value: '', label: '' },
            doctor: { value: '', label: '' },
            icd10CodesAreLoaded: false
        };
    }

    getFilteredICD10(): DropdownChoice[] {
        let filteredICD10: DropdownChoice[] = [];
        for (var i = 0; i < this.props.icd10Codes.length; i++) {
            let isSame = false;
            for (var j = 0; j < this.props.availment.diagnoses.length; j++) {
                isSame = (
                    this.props.availment.diagnoses[j].icd10Code.value === 
                    this.props.icd10Codes[i].value ? true : isSame);
            }
            if (!isSame) {
                filteredICD10 = filteredICD10.concat(this.props.icd10Codes[i]);
            }
        }
        return filteredICD10;
    }

    getProcedureCosts(procedures: ProcedureData[]) {
        let totalCost = 0;
        procedures.map(procedure => {
            totalCost += procedure.amount;
        });
        return totalCost;
    }
    
    pushBlankDiagnosis() {
        this.props.availment.diagnoses.push({
            icd10Code: this.state.icd10Code,
            remarks: '',
            isPrimary: false,
            isFinal: false,
            doctors: [{
                id: '',
                name: this.state.doctor.label,
                specialization: '',
                fee: 0,
                noOfVisits: 0,
                remarks: ''
            }],
            procedures: [{
                cptCode: { value: '', label: '' },
                isPHIC: false,
                amount: 0,
                notes: '',
                assessmentType: 'Pending',
                assessmentRemarks: ''
            }]   
        });
    }

    pushBlankProcedures(diagnosisIndex: number) {
        this.props.availment.diagnoses[diagnosisIndex].procedures.push({
            cptCode: { value: '', label: '' },
            isPHIC: false,
            amount: 0,
            notes: '',
            assessmentType: 'Pending',
            assessmentRemarks: ''
        });
    }

    render(): JSX.Element {
        return (
            <div className='availment-section'>
                <div className='sub-section'>
                    <div className='header-2'>AVAILMENT DETAILS</div>
                    <DetailedDropdown 
                        choices={this.props.facilities}
                        placeholder='Provider'
                        value={this.props.availment.provider}
                        error={
                            this.props.createTicketAttemp && 
                            this.props.availment.provider.value === ''}
                        onChange={(value: DropdownChoice) => {
                            this.props.availment.provider = value;
                            this.props.refreshAvailmentState();
                        }} />
                </div>
                <div className='sub-section'>
                    <div className='header-3'>LIST OF AVAILMENTS</div>
                    <div className='text'>
                        Add a diagnosis first then add procedures per diagnosis after
                    </div>
                    <div className='sub-section'>
                        <ComplexTable 
                            data={this.props.availment.diagnoses}
                            renderHeader={() => this.renderTableHeader()}
                            renderRow={(rowData: any, index: number) => 
                                this.renderTableRow(rowData, index)} />
                    </div>
                    {this.props.createTicketAttemp && this.props.availment.diagnoses.length == 0 ? (
                        <div className='sub-section' style={{color: '#E53935'}}>
                            No diagnosis. Need atleast one diagnosis.
                        </div> 
                    ) : (null)}
                </div>
                <div className='section' />
            </div>
        );
    }

    renderTableHeader(): JSX.Element {
        return (
            <>
                <Grid item xs={6}>
                    <DetailedDropdown 
                        choices={this.getFilteredICD10()}
                        value={this.state.icd10Code}
                        onChange={(value: DropdownChoice) => {
                            this.setState({icd10Code: value});
                        }} />
                </Grid>
                <Grid item xs={3}>
                    <Dropdown 
                        choices={this.props.doctors}
                        value={this.state.doctor}
                        onChange={(value: DropdownChoice) => {
                            this.setState({doctor: value});
                        }} />
                </Grid>
                <Grid item xs={3}>
                    <div style={{paddingTop: '1rem', textAlign: 'center'}}>
                        <TextButton 
                            icon={faPlus} 
                            label="Add diagnosis"
                            onClick={() => {
                                if (this.state.doctor.value != '' && this.state.icd10Code.value != '') {
                                    // this.props.availment.diagnoses.push({
                                    //     icd10Code: this.state.icd10Code,
                                    //     remarks: '',
                                    //     isPrimary: false,
                                    //     isFinal: false,
                                    //     doctors: [{
                                    //         id: '',
                                    //         name: this.state.doctor.label,
                                    //         specialization: '',
                                    //         fee: 0,
                                    //         noOfVisits: 0,
                                    //         remarks: ''
                                    //     }],
                                    //     procedures: [{
                                    //         cptCode: { value: '', label: '' },
                                    //         isPHIC: false,
                                    //         amount: 0,
                                    //         notes: '',
                                    //         assessmentType: 'Pending',
                                    //         assessmentRemarks: ''
                                    //     }]   
                                    // });
                                    this.pushBlankDiagnosis();
                                    this.props.refreshAvailmentState();
                                    this.setState({
                                        icd10Code: { value: '', label: '' },
                                        doctor: { value: '', label: '' }
                                    });
                                }
                            }} />
                    </div>
                </Grid>
            </>
        );
    }

    renderTableRow(diagnosis: DiagnosisData, index: number): JSX.Element {
        return (
            <>
                <div className='small-section'>
                    <Grid container spacing={1}>
                        <Grid item xs={6} className='header-2'>
                            {diagnosis.icd10Code.detail + ' - ' + diagnosis.icd10Code.label}
                        </Grid>
                        <Grid item xs={3} className='header-2'>
                            {diagnosis.doctors[0].name}
                        </Grid>
                        <Grid item xs={2} className='header-2'>
                            {GlobalFunction.formatNumber(this.getProcedureCosts(diagnosis.procedures)) + ".00 PHP"}
                        </Grid>
                        <Grid item xs={1}>
                            <TextButton
                                icon={faMinusCircle}
                                onClick={() => {
                                    this.props.availment.diagnoses.splice(index, 1);
                                    this.props.refreshAvailmentState();
                                }}/>
                        </Grid>
                    </Grid>
                </div>
                {diagnosis.procedures.map((procedure, procedureIndex) => {
                    return this.renderProcedureDetails(procedure, index, procedureIndex);
                })}
            </>
        );
    }
    
    renderProcedureDetails(procedure: ProcedureData, diagnosisIndex: number, index: number): JSX.Element {
        const stateProcedure = this.props.availment.diagnoses[diagnosisIndex].procedures[index];
        const stateProcedures = this.props.availment.diagnoses[diagnosisIndex].procedures;
        return (
            <div>
                <div className='line-border' />
                <div style={{padding: '1rem 1rem 2rem 1rem', position: 'relative'}}>
                    <Grid container spacing={2}>
                        <Grid item xs={8}>
                            <DetailedDropdown 
                                placeholder='Procedure'
                                choices={this.props.cptCodes}
                                value={procedure.cptCode}
                                onChange={(value: DropdownChoice) => {
                                    stateProcedure.cptCode = value;
                                    if (index == (stateProcedures.length - 1)) {
                                        this.pushBlankProcedures(diagnosisIndex);
                                    }
                                    this.props.refreshAvailmentState();
                                }} />
                        </Grid>
                        <Grid item xs={3}>
                            <NumberField
                                placeholder='Amount'
                                value={procedure.amount}
                                onChange={(value: number) => {
                                    stateProcedure.amount = value;
                                    this.props.refreshAvailmentState();
                                }} />
                        </Grid>
                        <Grid item xs={1}>
                            <CustomCheckbox
                                placeholder='PHIC'
                                value={procedure.isPHIC}
                                onChange={(value: boolean) => {
                                    stateProcedure.isPHIC = value;
                                    this.props.refreshAvailmentState();
                                }} />
                        </Grid>
                    </Grid>
                    <div style={{position: 'absolute'}}>
                        <TextButton 
                            label='Add procedure'
                            icon={faPlus}
                            onClick={() => {
                                this.pushBlankProcedures(diagnosisIndex);
                                this.props.refreshAvailmentState();
                            }}/>
                    </div>
                </div>
            </div>
        );
    }
}