import * as React from 'react';
import { ComplexTable } from 'Pages/CustomerCarePage/Components/ComplexTable';
import { Grid } from '@material-ui/core';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';

interface IStates {
    code: string;
    name: string;
    location: string;
}

export class PartnerNetworkSection extends React.Component<{}, IStates> {
    
    dummyData = [
        { code: '87854', name: 'St. Mary’s Hospital Calamba', location: 'CMC Ave, Real, Calamba, Laguna' },
        { code: '40092', name: 'Evangelista Medical Specialty Hospital', location: 'Macaria Drive, San Pedro, Laguna' },
        { code: '14659', name: 'Los Banos Doctors Hospital and Medical Center', location: 'Lopez Ave, Los Baños, Laguna' }
    ];

    constructor(props: any) {
        super(props);

        this.state = {
            code: '',
            name: '',
            location: ''
        }
    }

    getFilteredFacilitiesData = () => {
        return (
            this.dummyData
                .filter(facility => 
                    this.state.code === '' || 
                    facility.code.toLowerCase().includes(this.state.code.toLowerCase()))
                .filter(facility => 
                    this.state.name === '' || 
                    facility.name.toLowerCase().includes(this.state.name.toLowerCase()))
                .filter(facility => 
                    this.state.location === '' || 
                    facility.location.toLowerCase().includes(this.state.location.toLowerCase()))
        );
    }

    render(): JSX.Element {
        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Check Partner Network</div>
                <div className='sub-section'>
                    <div className='header-2'>TYPE</div>
                    <div className='small-text'>Open Door</div>
                </div>
                <div className='sub-section'>
                    <div className='header-2'>GROUP</div>
                    <div className='small-text'>*</div>
                </div>
                <div className='sub-section'>
                    <Grid className='sub-section' container spacing={2}>
                        <Grid item xs={2}>
                            <TextButton label='Excluded Hospitals' onClick={() => {}} />
                        </Grid>
                        <Grid item xs={2}>
                            <TextButton label='Included Hospitals' onClick={() => {}} />   
                        </Grid>
                    </Grid>
                    <ComplexTable 
                        data={this.getFilteredFacilitiesData()}
                        renderHeader={() => this.renderTableHeader()}
                        renderRow={(rowData: any, index: number) => 
                            this.renderTableRow(rowData, index)} />
                </div>
            </div>
        );
    }

    renderTableHeader(): JSX.Element {
        const headerNameStyle = {
            padding: '5px 0 0px 0'
        };

        return (
            <>
                <Grid item xs={2}>
                    <div style={headerNameStyle}>
                        <div className='header-2'>Facility Code</div>
                        <InputField 
                            value={this.state.code}
                            onChange={(value: string) => {
                                this.setState({code: value});
                            }} />
                    </div>
                    
                </Grid>
                <Grid item xs={6}>
                    <div style={headerNameStyle}>
                        <div className='header-2'>Name</div>
                        <InputField 
                            value={this.state.name}
                            onChange={(value: string) => {
                                this.setState({name: value});
                            }} />
                        
                    </div>
                    
                
                </Grid>
                <Grid item xs={4}>
                    <div style={headerNameStyle}>
                        <div className='header-2'>Location</div>
                        <InputField 
                            value={this.state.location}
                            onChange={(value: string) => {
                                this.setState({location: value});
                            }} />
                        
                    </div>
                    
                </Grid>
                
            </>
        );
    }

    renderTableRow(facility: any, _index: number): JSX.Element {
        const cellStyle = {
            // margin: 0,
            padding: '12px 0 12px 0'
            // fontWeight: 'bold'
        };

        return (
            <Grid container spacing={2} style={{padding: '0 20px 0 10px'}}>
                <Grid item xs={2}>
                    <div style={cellStyle}>{facility.code}</div>
                </Grid>
                <Grid item xs={6}>
                    <div style={cellStyle}>{facility.name}</div>
                </Grid>
                <Grid item xs={4}>
                    <div style={cellStyle}>{facility.location}</div>
                </Grid>
            </Grid>
        );
    }
}