import * as React from 'react';
import { TableCell, TableRow } from '@material-ui/core';
// import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
// import { ComplexTable } from 'Pages/CustomerCarePage/Components/ComplexTable';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { NumberField } from 'Pages/CustomerCarePage/Components/NumberField';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';
import { DropdownChoice, PartialTicketData } from 'Pages/CustomerCarePage/Models';
import { API } from 'Pages/CustomerCarePage/API';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';

interface IStates {
    ticketListUpdated: boolean;
}


export class ReviewTicketsSection extends React.Component<{}, IStates> {

    tickets: PartialTicketData[];

    constructor(props: any) {
        super(props);

        this.state = {
            ticketListUpdated: false
        }

        this.tickets = [];
    }

    componentDidMount() {
        API.get('/customer-care/tickets',
            (response) => {
                this.tickets = response.map(ticket => {
                    return {
                        ticketNo: '999999*',
                        urgency: ticket.ticket_category.urgency,
                        status: ticket.status,
                        channel: ticket.received_via,
                        type: ticket.ticket_category.ticket_type,
                        specificType: ticket.ticket_category.specific_type,
                        source: '*',
                        assignee: ticket.ticket_category.assign_to,
                        lastUpdate: new Date(ticket.date_updated),
                    }
                });
                console.log(response);
                this.setState({ticketListUpdated: true});
            },
            (error) => {
                console.log(error);
            });
    }

    render(): JSX.Element {
        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Review Tickets</div>
                <div className='sub-section' />
                {/* <Grid container className='sub-section' spacing={5}> */}
                <InputField 
                    value=''
                    onChange={(_value: string) => {

                    }} />
                <div className='sub-section'>
                    <CustomTable
                        className='custom-table-complex'
                        rows={this.tickets}
                        renderHeader={() => this.renderTableHeader()}
                        renderRow={(rowData: any) => this.renderTableRow(rowData)} />
                {/* <ComplexTable 
                    data={[0]}
                    renderHeader={() => this.renderTableHeader()}
                    renderRow={(rowData: any, index: number) => 
                        this.renderTableRow(rowData, index)} /> */}
                </div>
                {/* </Grid> */}
                
            </div>
        );
    }

    renderTableHeader(): JSX.Element {
        
        return (
            <>
            <TableCell>
                <div className='header-2'>Urgency</div>
                <NumberField
                    value={0}
                    onChange={(_value: number) => {

                    }}/>
            </TableCell>
            <TableCell>
                <div className='header-2'>Status</div>
                <InputField
                    value={'0'}
                    onChange={(_value: string) => {

                    }}/>
                
            </TableCell>
            <TableCell>
                <div className='header-2'>Ticket ID</div>
                <InputField
                    value={'0'}
                    onChange={(_value: string) => {

                    }}/>
            </TableCell>
            <TableCell>
                <div className='header-2'>Channel</div>
                <Dropdown
                    choices={[{value: '', label: ''}]}
                    value={{value: '', label: ''}}
                    onChange={(_value: DropdownChoice) => {

                    }}/>
            </TableCell>
            <TableCell>
                <div className='header-2'>Type</div>
                <Dropdown
                    choices={[{value: '', label: ''}]}
                    value={{value: '', label: ''}}
                    onChange={(_value: DropdownChoice) => {

                    }}/>
            </TableCell>
            <TableCell>
                <div className='header-2'>Specific Type</div>
                <Dropdown
                    choices={[{value: '', label: ''}]}
                    value={{value: '', label: ''}}
                    onChange={(_value: DropdownChoice) => {

                    }}/>
            </TableCell>
            <TableCell>
                <div className='header-2'>Source</div>
                <NumberField
                    value={0}
                    onChange={(_value: number) => {

                    }}/>
            </TableCell>
            <TableCell>
                <div className='header-2'>Assigned</div>
                <NumberField
                    value={0}
                    onChange={(_value: number) => {

                    }}/>
            </TableCell>
            <TableCell>
                <div className='header-2'>Last Update</div>
                <NumberField
                    value={0}
                    onChange={(_value: number) => {

                    }}/>
            </TableCell>
            </>
        );
    }

    renderTableRow = (ticket: PartialTicketData) => {
        return (
            <TableRow>
                <TableCell>{ticket.urgency}</TableCell>
                <TableCell>{ticket.status}</TableCell>
                <TableCell>{ticket.ticketNo}</TableCell>
                <TableCell>{ticket.channel}</TableCell>
                <TableCell>{ticket.type}</TableCell>
                <TableCell>{ticket.specificType}</TableCell>
                <TableCell>{ticket.source}</TableCell>
                <TableCell>{ticket.assignee}</TableCell>
                <TableCell>{GlobalFunction.toCommonDateString(ticket.lastUpdate)}</TableCell>
            </TableRow>
        );
    }
}