import * as React from 'react';
import { Table, TableBody, TableCell, TableHead, TableRow } from '@material-ui/core';
import './component-style.scss';

interface IProps {
    className: string;
    placeholder?: string;
    cols?: string[];
    rows: any;

    renderHeader?: () => JSX.Element;
    renderRow?: (rowData: any) => JSX.Element;

    /* 
     * Use this to stylize the column cells.
     * colIndex: the column index you want to style.
     * value: give you control to manipulate the value.
     */
    onFormat?: (colIndex: number, value: any) => any;

    /*
     * Use this to stylize the column headers.
     * colIndex: the column index you want to style.
     * name: the header name at will appear.
     */
    onHeaderFormat?: (colIndex: number, name: string) => JSX.Element;
}

export class CustomTable extends React.PureComponent<IProps> {

    constructor(props: IProps) {
        super(props);
    }

    renderCell(index: number, value: any): JSX.Element {
        return (
            <TableCell key={index}>
                {this.props.onFormat ? this.props.onFormat(index, value) : value}
            </TableCell>
        );
    }

    render(): JSX.Element {
        return (
            <div className={this.props.className}>
                {this.props.placeholder ? (
                    <div className='header-2'>{this.props.placeholder}</div>
                ) : null}
                <Table>
                    <TableHead>
                        <TableRow>
                            {this.props.renderHeader ? (
                                this.props.renderHeader()
                            ) : (
                                this.props.cols ? (
                                    this.props.cols.map((colName, index) => {
                                        return (
                                            this.props.onHeaderFormat ? (
                                                this.props.onHeaderFormat(index, colName)
                                            ) : (
                                                <TableCell key={index}>{colName}</TableCell>
                                            )
                                        );
                                    })
                                ) : null
                            )}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {this.props.rows.map((row, j) => {
                            const backgroundColor = (j % 2 == 0 ? '#FFFFFF' : '#F6F7FA');
                            
                            return this.props.renderRow ? (
                                this.props.renderRow(row)
                            ) : (
                                <TableRow key={j} style={{backgroundColor: backgroundColor}}>
                                    {Object.keys(row).map((value, j) => {
                                        return (this.renderCell(j, row[value]));
                                    })}
                                </TableRow>
                            )
                            
                        })}
                    </TableBody>
                </Table>
            </div>
        );
    }
}