import * as React from 'react';
import { Table, TableBody, TableCell, TableHead, TableRow } from '@material-ui/core';
import './component-style.scss';
import { TextButton } from './TextButton';
import { faPen, faMinusCircle, faCheckCircle, faTimesCircle } from '@fortawesome/free-solid-svg-icons';

interface IProps {
    className: string;
    placeholder: string;
    cols: string[];
    rows: string[][] | any;
    defaultRows?: number;

    onSaveEditRow: (rowIndex: number, value: any) => void;
    onDeleteRow: (rowIndex: number) => void; 

    /* 
     * Use this to stylize the column cells.
     * rowIndex: the row index you want to style.
     * colIndex: the column index you want to style.
     * value: give you control to manipulate the value.
     */
    onFormat?: (rowIndex: number, colIndex: number, value: any) => JSX.Element;

    /*
     * Use this to stylize the column headers.
     * colIndex: the column index you want to style.
     * name: the header name at will appear.
     */
    onHeaderFormat?: (colIndex: number, name: string) => JSX.Element;

    /* 
     * Use this when you want to stylize the column cell edit field
     * colIndex: the column index you want to style.
     * value: give you control to manipulate the value.
     * onChange: a function called when user tried to type/input.
     * - value: 
     */
    editField: (colIndex: number, value: any, onChange: (value: any) => void) => JSX.Element;
}

interface IStates {
    rowToEdit: number;
    data: any;
}

export class CRUDTable extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            rowToEdit: -1,

            data: {}
        }
    }

    handleOnChange = (attrName: string) => (value: any) => {
        this.setState(prevState => ({
            data: {
                ...prevState.data,
                [attrName]: value
            }
        }));
    }

    renderCell(row: number, column: number, attrName: string, value: any): JSX.Element {
        return (
            <TableCell key={column}>
                {this.state.rowToEdit == row ? (
                    this.props.editField ? this.props.editField(column, this.state.data[attrName], this.handleOnChange(attrName)) : value  
                ) : (
                    this.props.onFormat ? this.props.onFormat(row, column, value) : value
                )}
            </TableCell>
        );
    }

    render(): JSX.Element {
        return (
            <div className={this.props.className}>
                <div className='header-2'>{this.props.placeholder}</div>
                <div style={{overflowX: 'visible'}}>
                    <Table>
                        <TableHead>
                            <TableRow>
                                {this.props.cols.map((colName, index) => {
                                    return (
                                        this.props.onHeaderFormat ? (
                                            this.props.onHeaderFormat(index, colName)
                                        ) : (
                                            <TableCell key={index}>{colName}</TableCell>
                                        )
                                    );
                                })}
                                <TableCell className='last-column'>
                                </TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {this.props.rows.map((row, index1) => {
                                const backgroundColor = (index1 % 2 == 0 ? '#FFFFFF' : '#F6F7FA');
                                return (
                                    <TableRow style={{backgroundColor: backgroundColor}} key={index1}>
                                        {Object.keys(row).map((attrName, index2) => {
                                            return (this.renderCell(index1, index2, attrName, row[attrName]));
                                        })}
                                        <TableCell className='last-column'>
                                            {this.state.rowToEdit != index1 ? (
                                                <div>
                                                    <TextButton icon={faPen} onClick={() => {
                                                        this.setState({rowToEdit: index1});
                                                        this.setState({data: row});
                                                    }} />
                                                    <TextButton icon={faMinusCircle} onClick={() => {
                                                        this.props.onDeleteRow(index1)
                                                    }} />
                                                </div>
                                            ) : ( 
                                                <div>
                                                    <TextButton icon={faCheckCircle} onClick={() => {
                                                        this.props.onSaveEditRow(index1, this.state.data);
                                                        this.setState({rowToEdit: -1});
                                                    }} />
                                                    <TextButton icon={faTimesCircle} onClick={() => {
                                                        this.setState({rowToEdit: -1});
                                                    }} />
                                                </div>
                                            )}
                                        </TableCell>
                                    </TableRow>
                                );
                            })}
                        </TableBody>
                    </Table>
                </div>
            </div>
        );
    }
}